/**
 * 
 * @param {*} ctx canvas 
 * @param {object} option 
 */
const drawLine = (ctx, option) => {
    const {
        // 
        points,
        // 
        lineColor = '#76C1A6',
        // 
        lineWidth = 2,
        // [][2,2,2]https://developer.mozilla.org/en-US/docs/Web/API/CanvasRenderingContext2D/setLineDash
        lineDash = []
    } = option
    ctx.beginPath()
    ctx.setLineDash(lineDash)
    ctx.strokeStyle = lineColor
    ctx.lineWidth = lineWidth
    ctx.moveTo(points[0].x, points[0].y)
    ctx.lineTo(points[1].x, points[1].y)
    ctx.stroke()
}

export default drawLine