import getControlPoint from './util'

/**
 * 
 * @param {} ctx canvas
 * @param {object} option 
 */
const curve = (ctx, option) => {
    const {
        // 
        points,
        // 
        lineColor,
        // 
        lineWidth = 2,
        // 
        lineSmooth = 0.2,
        // [][2,2,2]https://developer.mozilla.org/en-US/docs/Web/API/CanvasRenderingContext2D/setLineDash
        lineDash = []
    } = option

    if(points.length <= 2) {
        console.warn('3!')
        return
    }
    ctx.beginPath()
    ctx.moveTo(points[0].x, points[0].y)
    ctx.strokeStyle = lineColor
    ctx.lineWidth = lineWidth
    ctx.setLineDash(lineDash)
    for(let i=1;i<points.length;i++) {
        var ctrlP = getControlPoint(points,i-1, lineSmooth)
        ctx.bezierCurveTo(ctrlP.pA.x, ctrlP.pA.y, ctrlP.pB.x,ctrlP.pB.y, points[i].x, points[i].y)
    }
    ctx.stroke()
  }

  export default curve