
/**
 * 
 * @param {*} center 
 * @param {*} distance 
 * @param {*} peakNum 
 * @param {*} startAngel 
 * @return {} 
 */
const getPeak = (center, peakNum, distance, startAngel) => {
    // 
    const corner = Math.PI * 2 / peakNum 
    const peaks = []
    for(let i=0;i<=peakNum;i++) {
        const x = center[0] + distance * Math.cos(startAngel)
        const y = center[1] - distance * Math.sin(startAngel)
        startAngel += corner
        peaks.push({x, y})
    }
    return peaks
}

export {
    getPeak
}