import merge from 'loadsh/merge'
import arrDetect from '../../util'
/**
 * 
 * @param {} ctx canvas
 * @param {object} option 
 */
const pie = (ctx, option) => {
    const {
        // ,[30, 40]
        center,
        // 
        radius,
        // 3
        startAngle = Math.PI * 0,
        // 
        endAngle = Math.PI * 2,
        // false 
        // *** 
        clockwise = true
    } = option
    // 
    if(arrDetect(center, '==2', '[100, 100]')) {
        return
    }
    // 
    if(!radius || typeof radius != 'number') {
        console.error('')
        return
    }
    // 
    // 
    const periphery = {
        // 
        show: false,
        // 
        color: '#8B8878',
        // 
        lineWidth: 2,
        // 
        lineDash: []
    }
    merge(periphery, option.periphery)
    // 
    const fill = {
        show: true,
        color: '#00F5FF'
    }
    merge(fill, option.fill)
    ctx.beginPath()
    // 
    ctx.setLineDash(periphery.lineDash)
    ctx.lineWidth = periphery.lineWidth
    ctx.strokeStyle = periphery.color
    // 
    ctx.fillStyle = fill.color
    // 
    ctx.arc(center[0], center[1], radius, startAngle, endAngle, clockwise)
    if(periphery.show) {
        ctx.stroke()
    }
    if(fill.show) {
        ctx.fill()
    }
}

export default pie