/**
 * 
 * @param {*} arr 
 * @param {*} compareLen < > == <= >=
 * @param {*} text 
 */
const arrDetect = (arr, compareLen, text) => {
    if(!arr) {
        console.error(text)
        return true
    }
    if(!(arr instanceof Array)) {
        console.error(text)
        return true
    }
    if(!eval(`${arr.length}${compareLen}`)) {
        console.error(text)
        return true
    }
    return false
}

export default arrDetect